import React from 'react';
import { connectEvaluation } from '../common/redux';

interface ICompEvaluationProps extends React.SVGProps<SVGSVGElement> {
  results: any;
  score: any;
  updateScore: any;
}

const FC: React.FC<ICompEvaluationProps> = ({ results, score, updateScore, ...props }) => {
  const [finished, submit] = React.useState(false);
  return (
    <div
      style={{
        position: 'fixed',
        top: 124,
        right: 0,
        paddingRight: 40,
        zIndex: 999,
      }}
    >
      <div
        style={{
          fontFamily: 'SaucerBB',
          fontSize: 80,
          color: 'red',
        }}
      >
        {100 + score}
      </div>
      <div
        style={{
          textAlign: 'center',
          fontWeight: 'bold',
          backgroundColor: 'yellow',
          padding: 4,
          border: '2px solid blue',
          cursor: 'pointer',
        }}
        onClick={() => {
          if (finished) return;
          const mark = results.reduce((mark: number, result: boolean) => {
            return mark + (100 / results.length) * ~~result;
          }, 0);
          updateScore(mark - 100);
          submit(true);
        }}
      >
        SUBMIT
      </div>
    </div>
  );
};

export const CompEvaluation = connectEvaluation(FC);
