import React, { SVGProps, useState, useEffect } from 'react';
import { connect } from 'redux-zero/react';
import Draggable from 'react-draggable';
import { IPageProps } from './';
import { CompDigit } from '../components/CompDigit';
import { CompEvaluation } from '../components/CompEvaluation';
import { CompAddition } from '../components/CompAddition';
import { connectDigit, connectEvaluation } from '../common/redux';
import { Util } from '../common/utils';
import logo from './draggable_4.svg';
const N = 20;
const MIN = 10;
const MAX = 100;

interface IPageDemoProps extends IPageProps {
  digitPanel: any;
  targetPanel: any;
  updateScore: any;
}

const FC: React.FC<IPageDemoProps> = (props) => {
  const [_, refresh] = React.useState(0);
  const { digitPanel, targetPanel, updateScore } = props;
  const problems = React.useMemo(() => [...Array(N)].map(() => [Util.random(MIN, MAX), Util.random(MIN, MAX)]), []);

  return (
    <>
      {/* <object data={logo} /> */}
      <div style={{ display: 'flex', position: 'fixed', backgroundColor: 'white', zIndex: 999 }}>
        {[...Array(10)].map((_, i) => (
          <CompDigit
            key={i}
            digit={i}
            draggable={true}
            onDragStart={(event: any) => targetPanel(null)}
            onDragEnd={(event: any) => {
              if (digitPanel) {
                const { validDigit, panel } = digitPanel;
                panel.innerHTML = '';
                panel.appendChild(Util.cloneDigit(event.target, validDigit, updateScore));
                refresh(Math.random());
              }
            }}
          />
        ))}
      </div>
      <CompEvaluation />
      <div style={{ position: 'absolute', top: 124, overflowY: 'scroll', width: '100%', height: 900 }}>
        <div style={{ display: 'flex', flexFlow: 'wrap', backgroundColor: 'linen' }}>
          {problems.map((operands, i) => (
            <CompAddition key={i} id={String(i)} operands={operands} />
          ))}
        </div>
      </div>
    </>
  );
};

export const PageDemo = connectEvaluation(connectDigit(FC));
