export namespace Util {
  export const random = (min: number, max: number) => ~~(Math.random() * (max - min)) + min;

  export const cloneDigit = (element: any, validDigit: string, updateScore: any) => {
    const digitElement = element.cloneNode(true);
    const check = digitElement.innerText === validDigit;
    digitElement.style.background = check ? 'green' : 'red';
    digitElement.ondragend = (event: any) => {
      const panel = document.createElement('div');
      panel.className = 'digit-panel';
      event.target.replaceWith(panel);
    };
    updateScore(~~check - 1);
    return digitElement;
  };
}
