import React from 'react';

interface ICompDigitPanelProps extends React.SVGProps<SVGSVGElement> {
  onDragOver: any;
  onDrop: any;
}

export const CompDigitPanel: React.FC<ICompDigitPanelProps> = ({ onDragOver, onDrop, ...props }) => {
  return (
    <div onDragOver={onDragOver} onDrop={onDrop}>
      <div className="digit-panel" />
    </div>
  );
};
